body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #374151;
}

.container {
  width: 80vw;
  margin: 4rem auto;
}

.input-field {
  display: flex;
  flex-direction: column;
  width: 16rem;
}

form {
  margin-bottom: 3rem;
}

label {
  margin-top: 1.25rem;
  margin-bottom: 0.4rem;
  font-weight: 700;
}

input {
  background: #fefefe;
  border: 1px solid #ccc;
  border-radius: 0.25rem;
  padding: 0.6rem 0.8rem;
  font-size: 1rem;
}

button {
  background: #047857;
  padding: 0.6rem 0.8rem;
  font-weight: 700;
  color: #fff;
  border: none;
  border-radius: 0.25rem;
  cursor: pointer;
}

.delete {
  background: #fff;
  border: 1px #ccc solid;
  color: #222;
  font-size: 0.6rem;
  padding: 0.3rem 0.4rem;
}

ul {
  list-style: none;
  padding: 0;
}
ul li {
  padding-bottom: 0.8rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.notification {
  display: flex;
  align-items: center;
  width: 100%;
  background-color: #ccc;
  border: 1px #aaa solid;
  border-radius: 0.3rem;
  font-size: 1.2rem;
  padding: 0.24rem 1.4rem;
  margin-bottom: 2rem;
  z-index: 10;
}

.success {
  background-color: #bfe1d7;
  border: 1px #047857 solid;
  color: #047857;
}

.error {
  background-color: #fef2f2;
  border: 1px #f87171 solid;
  color: #b91c1c;
}
